export default{
	computed:{
		tabbarList(){
			if(this.$store.getters.menuFlat){
				return this.$store.getters.menuFlat.slice(0,4);
			}	
		},
		officeActive(){
			for(let n in this.tabbarList){
				if(this.menu.path == this.tabbarList[n].path){
					return false
				}
			}
			return true
		},
		recordsAll(){
			return this.$store.state.table.recordsAll;
		}
	},
	props:{
		menu:{
			type:[Object,String]
		}
	},
	data(){
		return {
			windowHeight:'',
			showTabbar:true
		}
	},
	methods:{
		//点击tanbar 跳转
		async changeTab(menu){
			//跳转之前 是都有需要保存的数据
			if(this.recordsAll && this.recordsAll.length>0){
				uni.showModal({
				     content:'有未保存的修改，是否保存？',
					 cancelText:'不保存',
					 confirmText:'保存',
					 success:(res)=> {
					 	if(res.confirm){
							//保存所有表修改与添加
							this.$store.dispatch('saveOneTable',{tmpId:this.menu.tmpId,pageId:this.menu.pageId}).catch(error=>{});
							//重新加载index
							this.$store.dispatch("changeData",{pageId:this.menu.pageId})
							//检测是否有tableId为负数的行 然后移除
							this.$store.commit('initRows')	
							this.$store.commit('initTableState')
					 		//保存 修改
					 	}else if(res.cancel){
							// 不保存 直接返回
							this.$store.commit('initRows')	
							this.$store.commit('initTableState')
						}
					 }
				})
			}
			//切换菜单
			if(menu){
				this.$store.commit('updatePath',menu.path);
				let res = await this.$store.dispatch('getPath').catch(errpr=>{});
				if(res){
					uni.reLaunch({
						url:'/pages/index/index'
					})
				}else{
					uni.showToast({
						title:'页面请求失败',
						icon:'none'
					})
				}		
			}
		},
		toOfficePage(){
			uni.reLaunch({
				url:'/pages/office/office'
			})	
		},
		blurEdit(){
			this.$store.commit("initActiveItem");
			this.$store.commit("changeEditable",false);
			this.$store.commit("changeDeleteStatus",{tmpId:'',pageId:'',status:false});
		}
	},
	mounted() {
	    uni.getSystemInfo({
	        success: (res)=> {
	            this.windowHeight = res.windowHeight;
	        }
	    });    
	    uni.onWindowResize((res) => {
	        if(res.size.windowHeight+40 < this.windowHeight){
	            this.showTabbar = false
	        }else{
	            this.showTabbar = true
	        }
	    })
	}
}
